.Carouse-Multi-Slides {
    display: flex;
    justify-content: space-between;
}

.Controls {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
    width: 15%;
    margin-left: 76px;
    margin-right: 30px;
    &:hover{
        cursor: pointer;
    }
}

.Carousel-Items {
    width: 70%;
}

.Side-Carousel {
    display: flex;
    padding: 30px;
    min-height: 250px;
    align-items: center;
}

.SlideImage {
    max-width: 250px;
    padding-bottom: 20px;
    background: #f9f8f1;
}

.MiniSlideContent {
    display: flex;
    flex-direction: column;
    text-align: center;

    .h1-text {
        font-size: 19px;
    }
}

.font-50 {
    font-size: 2rem;
}